import React, { useEffect } from 'react'
import { Link, withRouter } from 'react-router-dom';
import MenuInferior from '../Components/MenuInferior';
import { getValueByKey} from '../services/utils'
import BasicHeader from '../Components/BasicHeader';
import { Switch } from 'react-materialize'


const Settings = ({ history }) => {

  const loginData = getValueByKey('loginData')
  const { username } = loginData

  useEffect(() => {
    if (loginData === 'emptyData') {
      history.push("/")
    }
  },[loginData])

  return (
    <div className="content-container">
      <BasicHeader title="Configurações" username={username} />

      <div className="form-container white1-background">
        {/* Input para muda o numero de telefone */}
        <p className="darkGrey-text"><strong>Mudar o nome de usuário:</strong></p>
        <input
          className=" darkGrey-text black-border"
          type="text"
          placeholder="Novo nome de usuário"
        />
        <p className="darkGrey-text"><strong>Mudar a senha</strong></p>
        <input
          className=" darkGrey-text"
          type="password"
          placeholder="Senha atual"
        />
        <input
          className=" darkGrey-text"
          type="password"
          placeholder="Nova senha"
        />
        <input
          className=" darkGrey-text"
          type="password"
          placeholder="Confrime a nova senha"
          /* Endpoint (envio de codigo de confirmacao no celular) */
        />
        <div className="suspender-switch">
          <p className="darkGrey-text"><strong>Suspender conta: </strong></p>
           <Switch />
        </div>
      </div>
      <MenuInferior />
    </div>
  )
}

export default withRouter(Settings)
